.App {
    font-family: sans-serif;
}


ol.Navbar {
    border: #ff81cc solid 2px;
    max-width: 20vh;
    margin-right: 10vh;
    margin-left: 10vh;
    -webkit-border-horizontal-spacing: 10vh;
    vertical-align: center;
    align-content: center;
}

ol.Navbar li {
    font-family: "Calibri";
    border: solid grey 2px;
    list-style-type: none;
    border-radius: 0%;
    cursor: pointer;
    height: 1vh;
    width: 10vw;
    min-height: 1vw;
    min-width: 5vh;
    text-align: center;
    vertical-align: middle;
}
li.selected {
    background: yellowgreen;
}
ol.Navbar li:hover{
    box-shadow: inset 0px 11px 8px -10px #337AB7,
    inset 0px -11px 8px -10px #337AB7;

}